body {
  margin: 0;
  color: white;
  background-color: #161616;
  font-family: "Gowun Dodum", sans-serif;
  align-items: center;
}

.App {
}

.section {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s ease, transform 1s ease;
}

.section.visible {
  opacity: 1;
  transform: translateY(0);
}

/* 전역 스타일링 */

h1 {
  margin: 20px;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: calc(25px + 1.5vw);
}

h2 {
  margin: 0 0 30px 0;
  font-family: "Poppins", sans-serif;
  font-size: calc(15px + 1vw);
}

h3 {
  margin: 0;
}

h4 {
  margin: 0;
  color: rgba(137, 137, 143, 0.712);
}

.container {
  padding: 80px 30px;
  margin: 0;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.card-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 0px;
}

footer {
  margin: 20px 0;
  text-align: center;
  font-size: 15px;
}

@media screen and (min-width: 280px) and (max-width: 750px) {
  .card-wrapper {
    align-items: center;
    margin: 0px;
    flex-direction: column;
  }
}
